.cards {
  clear: both; }

.card {
  float: left;
  border: 1px solid black;
  width: 70px;
  height: 100px;
  margin: 10px;
  list-style: none; }
  .card__content--closed {
    width: 100%;
    height: 100%;
    background-color: gray; }
  .card__suit--h, .card__suit--d {
    color: red; }



